<template>
  <div class="va-chat">
    <div
      class="va-chat__body"
      :style="{ height: height, backgroundColor: '#00000017' }"
      v-sticky-scroll="{
        animate: true,
        duration: 200,
      }"
    >
      <div
        class="va-chat__message"
        v-for="(message, index) in value"
        :key="index"
        :style="{
          ...(message.yours ? computedYoursStyle : computedStyle),
          paddingTop: !message.delivered ? '1.4rem' : '0.657rem',
        }"
        :class="{ 'va-chat__message--yours': message.yours }"
      >
        <div
          class="va-chat__message-info"
          :style="{
            left: message.yours ? 'auto' : '0.5rem',
            right: message.yours ? '0.5rem' : 'auto',
          }"
        >
          <va-icon
            name="fa fa-spinner fa-spin"
            v-if="!message.delivered"
          ></va-icon>
        </div>

        <span
          class="va-chat__message-text"
          :style="{
            textAlign: message.yours ? 'right' : 'left',
          }"
        >
          {{ message.text }}
        </span>
        <span
          class="va-chat__message_date"
          :style="{ opacity: 0.4, textAlign: message.yours ? 'right' : 'left' }"
        >
          {{ message.date }}
        </span>
      </div>
    </div>
    <div class="va-chat__controls">
      <va-input
        :autosize="true"
        v-model="inputMessage"
        @keypress.enter="sendMessage"
        :placeholder="$t('dashboard.support.placeholder')"
        class="va-chat__input"
      />
      <va-button @click="sendMessage">
        {{ $t('buttons.send') }}
      </va-button>
    </div>
  </div>
</template>

<script>
import { StickyScroll } from '../../../services/vuestic-ui/components'

export default {
  name: 'chat',
  directives: { StickyScroll },
  data() {
    return {
      inputMessage: '',
    }
  },
  computed: {
    computedStyle() {
      return { backgroundColor: this.$themes.darkForeground, color: 'white' }
    },
    computedYoursStyle() {
      return { backgroundColor: this.$themes.info, color: 'white' }
    },
  },
  props: {
    value: Array,
    height: {
      default: '20rem',
      type: String,
    },
  },
  watch: {
    value() {
      const body = this.$el.querySelector('.va-chat__body')
      body.scrollTop = body.scrollHeight
    },
  },
  methods: {
    sendMessage() {
      if (!this.inputMessage) {
        return
      }
      this.$emit('input', {
        text: this.inputMessage,
        yours: true,
      })
      this.inputMessage = ''
      setTimeout(() => {
        this.$el.querySelector('.va-chat__input').focus()
      }, 10)
    },
  },
}
</script>

<style lang="scss">
.chat {
  &__content {
    @include va-flex-center();
  }
}

$chat-message-br: 0.65rem;

.va-chat {
  width: 100%;
  padding: 0;

  &__body {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    overflow-y: auto;
    padding: 20px 10px;
  }

  &__message {
    position: relative;
    padding: 0.657rem 1.375rem;
    margin-bottom: $chat-message-br;
    max-width: 70%;
    overflow-wrap: break-word;
    border-radius: 0.5rem;
    border-top-left-radius: 0;
    align-self: flex-start;
    background-color: $light-gray2;
    transition: padding 0.3s;

    &-info {
      position: absolute;
      top: 0.4rem;
      font-size: 0.6rem;
    }

    &-text {
      display: block;
      transform: translateY(-2px);
    }

    &:last-child {
      margin-bottom: 0;
    }

    &--yours {
      align-self: flex-end;
      border-top-right-radius: 0;
      border-top-left-radius: 0.5rem;
    }

    &_date {
      font-family: sans-serif;
      font-size: 0.8rem;
      opacity: 0;
      transition: opacity 0.5s;
    }
  }

  &__controls {
    display: flex;
    align-items: center;
    height: 2rem;
    padding: 2.6rem 0;
  }

  &__input {
    margin-bottom: 0;
    color: white;
  }
}
</style>
